<template>
  <div class="betting-365">
    <p class="betting-365-title" :class="{rotate:rotate}" @click="rotate=!rotate">{{ item.round|inning }}</p>
    <ul class="betting-365-body" v-show="rotate">
      <li v-for="n in item.guess_list" :key="n.smpid" :class="initialTitle(n,true)">
        <p :class="initialTitle(n)"><span>{{ n.play_name }}</span></p>
        <div class="Ranks" :class="prizeStatus(true,n)">
          <div>{{ n.host_team_name }}</div>
          <div></div>
          <div @click="betClick(0,n)">{{ initialOdd(true,n.odds) }}</div>
        </div>
        <div class="Ranks" :class="prizeStatus(false,n)">
          <div>{{ n.guest_team_name }}</div>
          <div></div>
          <div @click="betClick(1,n)">{{ initialOdd(false,n.odds) }}</div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>//
import { token } from '../../tool/storeManage'
import { initStatus, oddsFiltration } from '../../tool/util'
import { Toast } from 'mint-ui'
import { mapState } from 'vuex'
import { InitScore } from '../../tool/filter'

export default {
  name: 'Betting365',
  props: {
    item: { type: Object },
    status: { type: Number }
  },
  data () {
    return {
      rotate: true
    }
  },
  computed: {
    ...mapState({
      Pattern: state => state.home.Pattern[state.home.PatternIndex] || {},
      Betting: state => state.home.PassStatus.Betting
    }),
    patternId () {
      return this.Pattern.id || parseInt(this.$route.query.patternId)
    }
  },
  methods: {
    initStatus (status, time) {
      return initStatus(status, time)
    },
    prizeStatus (HorG, n) {
      let className = ''
      if (HorG) {
        // if (this.patternId !== 2 && n.bet_num.indexOf('1') > -1) className += `state-2`// 投注图标
        if (this.status > 1 && n.prize_status > 0 && parseInt(n.prize_result) === 1) className += ` state-1` // 胜利者
      } else {
        // if (this.patternId !== 2 && n.bet_num.indexOf('2') > -1) className += `state-2`
        if (this.status > 1 && n.prize_status > 0 && parseInt(n.prize_result) === 2) className += ` state-1`
      }
      return className
    },
    initialTitle (n, li) {
      let className = ''
      if (li) {
        // 计算li显示css
        if (n.status === 0 && (n.is_sell < 1 || n.odds[0].sell_status < 1 || n.odds[1].sell_status < 1)) {
          // 投注关闭
          className += `betting-close `
        } else if (!(n.prize_status > 0 && n.prize_result === '') && n.status === 0) {
          className += `betting `
          // 如果是过关
          if (this.Betting[n.smid]) {
            // if (this.patternId === 2 && this.Betting[n.smid]) {
            // 有没有添加到竞猜单列表如果就显示出来
            if (this.Betting[n.smid][`${n.smpid}0`]) {
              className += `betting-0 `
            }
            if (this.Betting[n.smid][`${n.smpid}1`]) {
              className += `betting-1 `
            }
          }
        }
        return className
      }
      // 计算p显示css
      if (n.status === 0 && (n.is_sell < 1 || n.odds[0].sell_status < 1 || n.odds[1].sell_status < 1)) {
        // 投注关闭
        className += `state-close state-1 `
      } else if (n.prize_status > 0 && n.prize_result === '') {
        // 取消状态
        className += `state-null state-2 `
      } else {
        className += `state-${n.status} `
      }
      return className
    },
    initialOdd (HorG, odd) {
      return oddsFiltration(odd, HorG)
    },
    betClick (bet, n) {
      const query = this.$route.query
      if (!token.Token) {
        Toast('请登陆')
        this.$router.replace({
          path: '/login',
          query: { ...(query), back: true, redirect: this.$route.path }
        })
        return
      }
      if (n.is_sell < 1 || this.initStatus(n.status, n.game_start_time) !== 0) {
        Toast('这场已经停止竞猜')
        return
      }
      this.$emit('betting', {
        bet,
        item: n
      })
    }
  },
  filters: {
    inning: InitScore
  }
}
</script>

<style scoped lang="scss">
  .betting-365 {
    .betting-365-title {
      color: #ccc;
      background-color: #313131;
      border-radius: 4px;
      margin: 0 10px 5px 10px;
      text-align: center;
      height: 26px;
      line-height: 26px;
      &::after {
        content: '';
        position: absolute;
        right: 15px;
        display: inline-block;
        width: 26px;
        height: 26px;
        background-image: url("../../assets/mine/ic_go@2x.png");
        background-repeat: no-repeat;
        background-position: center center;
        background-size: 100%;
      }
      &.rotate::after {
        transform: rotate(90deg);
      }
    }
    .betting-365-body {
      > li {
        margin: 0 10px 5px 10px;
        > p:nth-child(1) {
          background: rgba(28, 28, 28, 1);
          border-radius: 4px 4px 0 0;
          position: relative;

          > span:nth-child(1) {
            display: inline-block;
            color: #FFC63A;
            width: 44%;
            max-width: 44%;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            line-height: 26px;
            vertical-align: bottom;
          }
          &::before {
            content: '';
            display: inline-block;
            line-height: 26px;
            width: 20%;
            margin-right: 2%;
            text-align: center;
            border-radius: 4px 4px 0 0;
          }
          &.state-0::before {
            content: '可竞猜';
            background-color: #1AC456;
            color: #FFFFFF;
          }
          &.state-1::before {
            content: '待开奖';
            background-color: #FFC63A;
            color: #131313;
          }
          &.state-2::before, &.state-3::before {
            content: '已结算';
            background-color: #DDDDDD;
            color: #131313;
          }
          &.state-null::after {
            content: '';
            display: inline-block;
            width: 34%;
            height: 26px;
            background-image: url("../../assets/competition/365/ic_delll.png");
            background-repeat: no-repeat;
            background-position: center center;
            background-size: 42px 13px;
            float: right;
          }

          &.state-close::after {
            content: '投注已关闭';
            display: inline-block;
            width: 34%;
            height: 26px;
            line-height: 26px;
            text-align: center;
            color: #FFC63A;
            background-color: #999999;
            float: right;
            border-top-right-radius: 4px;
          }
        }

        &.betting > div.Ranks > div:nth-child(3) {
          background: linear-gradient(180deg, rgba(63, 63, 63, 1) 0%, rgba(49, 49, 49, 1) 100%);
        }
        &.betting-0 > div.Ranks:nth-of-type(1) > div:nth-child(3), &.betting-1 > div.Ranks:nth-of-type(2) > div:nth-child(3) {
          background: linear-gradient(0deg, rgba(153, 153, 153, 1) 0%, rgba(204, 204, 204, 1) 100%);
          color: #131313;
        }
        > div.Ranks {
          &:last-child {
            border-radius: 0 0 4px 4px;
          }
          &:nth-of-type(1) {
            border-bottom: 1px solid #494949;
          }
          display: flex;
          flex-flow: row nowrap;
          background: rgba(49, 49, 49, 1);
          align-items: center;
          height: 35px;
          > div:nth-child(1) {
            width: 44%;
            flex: 1;
            text-indent: 1em;
            overflow: hidden;
            text-overflow: ellipsis;
          }
          > div:nth-child(2) {
            display: none;
            width: 22%;
            text-align: center;
          }
          > div:nth-child(3) {
            width: 34%;
            text-align: center;
            line-height: 34px;
            border-left: 1px solid #494949;
            position: relative;
          }
          &.state-1 > div:nth-child(2) {
            display: inline-block;
          }
          &.state-1 > div:nth-child(2)::after {
            /*冠军*/
            display: inline-block;
            background-repeat: no-repeat;
            background-position: center center;
            background-size: 100%;
            content: '';
            vertical-align: middle;
            width: 20px;
            height: 20px;
            background-image: url("../../assets/competition/365/ic_win.png");
          }
          &.state-2 > div:nth-child(3)::after {
            /*投注*/
            content: '';
            position: absolute;
            right: 10px;
            top: 50%;
            transform: translate(0, -50%);
            display: inline-block;
            width: 17px;
            height: 17px;
            background-image: url("../../assets/competition/365/ic_sel.png");
            background-repeat: no-repeat;
            background-position: center center;
            background-size: 100%;
          }
        }
      }
    }
  }
</style>
