  created () {
    this.initial()
  },
  methods: {
    initial () {
      /*
      * gameId 游戏类别id
      * smid 赛事id
      * matchStatus 1赛程和进行中   2赛果和已结束
      * patternId 是 过关或早盘id
      */
      http.getMatchInfo(this.$route.query.smid, this.$route.query.gameId).then(value => {
        value && (this.MatchInfo = value)
        return http.getMatchGuess(this.$route.query.smid, this.$route.query.gameId, this.$route.query.matchStatus)
      }).then(value => {
        if (Array.isArray(value) && value.length) {
          this.MatchOdds = value
        }
      }).finally(() => {
        if (this.MatchOdds.length === 0) {
          this.noContent = '2'
        }
      })
      if (token.Token) {
        this.$store.dispatch(GET_USER_INFO)
      }
    },
    switchBody (act) {
      if (act === this.navIndex[0]) return
      if (act === 1) {
        this.getTeamAnalysisData().then(() => {
          this.noContent = ''
          this.navIndex = [1, true]
        })
      } else if (act === 2) {
        this.$set(this.navIndex, 0, act)
        this.noContent = '3'
      } else {
        this.noContent = this.MatchOdds.length ? '' : '2'
        this.$set(this.navIndex, 0, act)
      }
    },
    getTeamAnalysisData () {
      return http.getTeamAnalysisData(this.$route.query.smid, this.MatchInfo.host_team_id, this.MatchInfo.guest_team_id).then(value => {
        if (value) {
          this.Analysis = value
        }
      })
    },
    betting ({ bet, item }) {
      // 投注
      const item1 = { ...item }
      item1.bet_now = bet // 目标0是主队1是客队
      this.$store.commit('ADD_DEL_365_THROUGH_DATA', { item: item1, patternId: this.$route.query.patternId })
    },
    openLive (title, url) {
      if (this.Device.navInfo.ios) {
        window.location.href = url
      } else {
        this.$router.push({
          path: '/webPage',
          query: { title: title, url: url }
        })
      }
    }
  },
  computed: {
    ...mapState({
      Device: state => state.mine.Device,
      PassStatus: state => state.home.PassStatus
    }),
    showContent () {
      if (this.navIndex[0] === 0) {
        return ['这场比赛没有开放竞猜!', '看看其它的比赛吧!']
      } else {
        return ['这场比赛没有视频回放!', '看看其它的比赛吧!']
      }
    },
    inning () {
      return InitScore(this.MatchInfo.curr_round)
    },
    initialTitle () {
      if (this.initialStatus !== 1) {
        return `${timeFmt(formatTimeStamp(this.MatchInfo.game_start_time), 'MM-dd hh:mm')}  ${this.initDescribe}`
      }
      return this.initDescribe
    },
    initDescribe () {
      const st = this.initialStatus
      if (typeof st === 'number' && st !== -1) {
        if (st === 0) {
          return '开始'
        } else if (st === 1) {
          return '进行中'
        }
        return '已结束'
      }
      return '加载中..'
    },
    initialStatus () {
      if (this.MatchInfo.status !== undefined && this.MatchInfo.game_start_time) {
        return initStatus(this.MatchInfo.status, this.MatchInfo.game_start_time)
      }
      return -1
    }
  },
  filters: {
    score,
    toFixed
  }
}
