<template>
  <div>
    <nav-head title="竞猜单" fixed-or-transparency="isFixed NoTransparency">
      <span slot="center"
            style="font-size: 12px;display: inline-block;background-color: #FFC63A;color: #131313;border-radius: 50%;line-height: 2em;margin-left: 3px;width: 2em">
        {{ PassArray.length }}</span>
      <div @click="emptyBet" slot="right"
           style="line-height: 46px;font-size: 14px;color: #FFC63A;padding: 0 13px;font-weight: bold;">清空
      </div>
    </nav-head>
    <ul style="margin-top: 10px">
      <no-content
        tag="li"
        v-show="backShow&&PassArray.length===0"
        img="2"
        :text="['没有比赛!','去其添加吧!']"
      />
      <pass-betting
              :item="n"
              :key="`${n.smpid}${n.smid}${n.bet_now}`"
              @deletePASS="deletePASS({item:n,patternId:$route.query.patternId})"
              v-for="n in PassArray"
      >
        <template v-if="early">
          <!--早盘-->
          <div class="early-bet">
            <p>预计收益: <span style="color: #FF3333">{{((n.Amount||1)*n.odds[n.bet_now].odds/1000).toFixed(2)}}</span></p>
            <div :class="{have:n.Amount}" @click="ShowKey($event,n)">{{n.Amount||'请输入金额'}}</div>
          </div>
          <router-link
                  :to="{path:'/redPacketsSelected',query:{ supportType:1, activityId:0, target:`${n.smid}${n.smpid}${n.bet_now}` }}"
                  class="red-envelopes red-item"
                  tag="div">
            <p>红包</p>
            <p>{{filtrationRed(`${n.smid}${n.smpid}${n.bet_now}`)}}</p>
          </router-link>
        </template>
      </pass-betting>
    </ul>
    <div :style="{height: `${KeyboardHeight}px`}">
      <div class="input-bet">
        <template v-if="$route.query.patternId*1===2">
          <!--过关-->
          <div class="top">
            <p>{{ PassArray.length ? `${PassArray.length}串1`:'暂无' }}</p>
            <p>投注封顶</p>
            <p>100000</p>
          </div>
          <div class="center">
            <div>
              <div @click="ShowKey">{{ earlyItem.Amount||'请输入金额' }}</div>
              <p>总赔率:</p>
            </div>
            <p>{{ PassStatus.odds.toFixed(2) }}</p>
          </div>
          <div class="bottom">
            <p><span>预计收益</span><span>{{(earlyItem.Amount*PassStatus.odds).toFixed(2) }}</span></p>
            <p><span>我的余额:</span><span>{{ UserInfo.diamond }}</span></p>
          </div>
          <!--<router-link :to="{path:'/redPacketsSelected',query:{ supportType:1, activityId:0, target:`pass` }}"-->
                       <!--class="red-envelopes">-->
            <!--<p>红包</p>-->
            <!--<p>{{filtrationRed('pass')}}</p>-->
          <!--</router-link>-->
        </template>
        <template v-else-if="early">
          <!--早盘-->
          <div class="early-top">
            <p>我的余额 <span style="float: right">{{UserInfo.diamond}}</span></p>
            <p>投注封顶 <span style="float: right">100000</span></p>
          </div>
        </template>
        <div :class="'button-'+$route.query.patternId">
          <button @click="$router.back()">继续添加</button>
          <button @click="checkBet">确认投注</button>
        </div>
        <number-key-board
                :amount="earlyItem.Amount"
                @update:amount="UpdateSum"
                v-show="ShowKeyboard"
        />
      </div>
      <div v-show="ShowKeyboard" @click="ShowKey" class="own-modal" :style="{height:Device.height+'px'}"></div>
    </div>
  </div>
</template>

<script>//
import { mapActions, mapMutations, mapState } from 'vuex'
import { GET_COUPON_LIST, GET_USER_INFO } from '../../../../vuex/Mine'
import { ADD_DEL_365_THROUGH_DATA, ADD_EARLY_BET_SUM, MODIFY_365_DATA } from '../../../../vuex/365/home'
import { MessageBox, Toast } from 'mint-ui'
import { token } from '../../../../tool/storeManage'
import http from '../../../../tool/http'
import { formatTimeStamp } from '../../../../tool/util'
import PassBetting from '../../../../components/365/PassBetting'
import NumberKeyBoard from '../../../../components/365/NumberKeyBoard'

export default {
  name: 'GuessForm',
  components: { NumberKeyBoard, PassBetting },
  data () {
    const early = this.$route.query.patternId * 1 === 1 // 早盘模式
    return {
      early,
      earlyItem: { Amount: 0 }, // 过关投注金额
      ShowKeyboard: false, // 数字键盘
      KeyboardHeight: early ? 74 : 184, // 上拉高度
      backShow: true // 闪现无比赛处理
    }
  },
  computed: {
    ...mapState({
      Device: state => state.mine.Device,
      UserInfo: state => state.mine.User,
      PassStatus: state => state.home.PassStatus,
      RedPackets: state => state.home.PassStatus.UseRed,
      PassArray: state => {
        // 对投注对象进行序列化
        const array = []
        for (let i in state.home.PassStatus.Betting) {
          if (state.home.PassStatus.Betting[i]) {
            for (let j in state.home.PassStatus.Betting[i]) {
              array.push(state.home.PassStatus.Betting[i][j])
            }
          }
        }
        return array
      }
    })
  },
  methods: {
    ...mapActions({
      getUserInfo: GET_USER_INFO,
      getCouponList: GET_COUPON_LIST
    }),
    ...mapMutations({
      deletePASS: ADD_DEL_365_THROUGH_DATA,
      modifyPass: MODIFY_365_DATA,
      updateItemSum: ADD_EARLY_BET_SUM
    }),
    ShowKey (event, item) {
      event.stopPropagation()
      if (this.ShowKeyboard) {
        this.early && (this.earlyItem = { Amount: 0 })// 早盘
        this.KeyboardHeight -= 188
        this.ShowKeyboard = false
      } else {
        this.early && (this.earlyItem = item)// 早盘选中的比赛
        this.KeyboardHeight += 188
        this.ShowKeyboard = true
      }
    },
    UpdateSum (number) {
      if (this.early) {
        // 早盘
        const item = { ...this.earlyItem }
        item.Amount = number
        this.earlyItem = item
        this.updateItemSum({ target: item, sum: number }) // 更新比赛投注金额
      } else {
        this.earlyItem = { Amount: number }
      }
    },
    emptyBet () {
      MessageBox({
        title: '',
        message: '确认清空场次吗?',
        showCancelButton: true,
        confirmButtonText: `清空`
      }).then(action => {
        if (action === 'confirm') {
          this.deletePASS({ initial: true, patternId: this.$route.query.patternId })
          this.$router.back()
        }
      })
    },
    checkBet (event) {
      this.ShowKeyboard && this.ShowKey(event)// 关闭键盘
      // 验证
      const gameSession = this.early ? 1 : 2
      if (this.PassArray.length < gameSession) {
        Toast(`最少${gameSession}场比赛,才能下注`)
        return
      }
      if (!token.Token) {
        Toast('请登陆')
        this.$router.replace({ path: '/login' })
        return
      }
      if (this.early) {
        // 早盘
        let sumUp = 0 // 总投注金额
        let sumUpRed = 0 // 红包总金额
        const noAmount = this.PassArray.every((i, index) => {
          if (!i.Amount) {
            Toast(`第${index + 1}场投注请输入金额`)
            return
          }
          if (i.Amount > 100000) {
            Toast(`第${index + 1}场投注金额超出限制`)
            return
          }
          if (this.RedPackets[`${i.smid}${i.smpid}${i.bet_now}`]) {
            // 有选中红包
            if (this.RedPackets[`${i.smid}${i.smpid}${i.bet_now}`].coupon_min_consume_price > i.Amount) {
              Toast(`第${index + 1}场,红包需要满${this.RedPackets[`${i.smid}${i.smpid}${i.bet_now}`].coupon_min_consume_price}元才能使用`)
              return
            }
            sumUpRed += parseFloat(this.RedPackets[`${i.smid}${i.smpid}${i.bet_now}`].amount)
          }
          sumUp += i.Amount
          return true
        })
        if (!noAmount) {
          return
        }
        if (sumUp > (parseFloat(this.UserInfo.diamond) + sumUpRed)) {
          MessageBox({
            title: '',
            message: '余额不足,是否去充值?',
            showCancelButton: true,
            confirmButtonText: `充值`
          }).then(action => {
            if (action === 'confirm') {
              this.$router.push({ path: '/recharge' })
            }
          })
          return
        }
      } else {
        // 过关
        if (!this.earlyItem.Amount) {
          Toast('请输入投注金额')
          return
        }
        if (this.earlyItem.Amount > 100000) {
          Toast('投注金额超出限制')
          this.$set(this.earlyItem, 'Amount', 0)
          return
        }
        if (this.RedPackets.pass && this.RedPackets.pass.coupon_min_consume_price > this.earlyItem.Amount) {
          Toast(`红包需要满${this.RedPackets.pass.coupon_min_consume_price}元才能使用`)
          return
        }
        if (this.earlyItem.Amount > (this.RedPackets.pass ? parseFloat(this.UserInfo.diamond) + parseFloat(this.RedPackets.pass.amount) : this.UserInfo.diamond)) {
          MessageBox({
            title: '',
            message: '余额不足,是否去充值?',
            showCancelButton: true,
            confirmButtonText: `充值`
          }).then(action => {
            if (action === 'confirm') {
              this.$router.push({ path: '/recharge' })
            }
          })
          return
        }
      }
      // 验证完毕 开始支付
      this.Play()
    },
    orderContent () {
      let timeError = true
      const arr = []
      for (let i in this.PassArray) {
        if (this.PassArray[i].betCustomStatus) {
          timeError = false
          break
        } else if (timeError) {
          const item = {
            'host_team_odd': this.PassArray[i].odds[0].odds / 1000,
            'guest_team_odd': this.PassArray[i].odds[1].odds / 1000,
            'bet_match_type': this.PassArray[i].match_type,
            'bet_number': this.PassArray[i].bet_now + 1,
            'bet_odds': this.PassArray[i].odds[this.PassArray[i].bet_now].odds,
            'bet_game_id': this.PassArray[i].game_type_id,
            'bet_team_name': this.PassArray[i].bet_now === 0 ? this.PassArray[i].host_team_name : this.PassArray[i].guest_team_name,
            'bet_match_name': this.PassArray[i].game_type_name,
            'bet_play_name': this.PassArray[i].play_name,
            'bet_play_id': this.PassArray[i].play_id,
            'bet_round': this.PassArray[i].round,
            'bet_smid': this.PassArray[i].smid,
            'bet_smpid': this.PassArray[i].smpid,
            'host_team_name': this.PassArray[i].host_team_name,
            'guest_team_name': this.PassArray[i].guest_team_name,
            'game_start_time': formatTimeStamp(this.PassArray[i].game_start_time)
          }
          if (this.early) {
            item.stake = this.PassArray[i].Amount
            item.coupon_id = this.RedPackets[`${this.PassArray[i].smid}${this.PassArray[i].smpid}${this.PassArray[i].bet_now}`] && this.RedPackets[`${this.PassArray[i].smid}${this.PassArray[i].smpid}${this.PassArray[i].bet_now}`].id
          }
          arr.push(item)
        }
      }
      if (timeError === false) return timeError
      return arr.sort((t, a) => t.game_start_time > a.game_start_time)
    },
    Play () {
      const Content = this.orderContent()
      if (Content === false) {
        Toast('有比赛已禁止投注,请删除')
        return
      }
      const post = this.early ? http.postMatchSinglePassBet(Content) : http.postMatchSkipMatchBet(this.earlyItem.Amount, Content, /* 红包 */this.RedPackets.pass ? this.RedPackets.pass.id : '')
      post.then(val => {
        if (val) {
          this.backShow = false // 解决闪现
          this.getCouponList()
          this.getUserInfo()
          Toast('下注成功')
          this.deletePASS({ initial: true, patternId: this.$route.query.patternId })
          this.$router.back()
        }
      }).catch(val => {
        let data = {}
        try {
          data = JSON.parse(val.message)
        } catch (e) {
          Toast('网络错误')
        }
        if (Array.isArray(data.data) && data.data.length) {
          this.modifyPass(data.data)
        }
      })
    },
    filtrationRed (key) {
      if (!this.RedPackets[key]) {
        return this.RedPackets[key] === 0 ? '不使用红包' : '选择红包'
      }
      return `${this.RedPackets[key].amount}红包`
    }
  },
  created () {
    this.getUserInfo()
  }
}
</script>

