<template>
  <div class="betting-details">
    <nav-head title="投注详情" FixedOrTransparency="NoTransparency"/>
    <div class="order-list-entry">
      <img :src="srcTxt(details.order_list.length)" width="40" height="40" alt="">
      <div>
        <div>
          <p>{{ srcTxt(details.order_list.length,true) }}</p>
          <p :class="status(details.action_type,details.distribute_status,details.winnings_status,true)"></p>
        </div>
        <div>
          <p>投注金额: {{ details.total_amount }}</p>
          <p :class="status(details.action_type,details.distribute_status,details.winnings_status,true)"><span>{{ details.winnings_bonus }}</span>
          </p>
        </div>
      </div>
    </div>
    <div class="betting-form-connect">
      <img src="../../../assets/store/365/bg.png" alt="">
    </div>
    <div class="betting-form" style="margin: 0 10px 10px 10px">
      <div class="form-head">
        <p>投注时间:{{ time(details.bet_time) }}</p>
        <p>总赔率: {{ details.total_odds }}</p>
      </div>
      <table>
        <tr key="keyOrder_list">
          <th>时间</th>
          <th>游戏</th>
          <th>比赛队伍</th>
          <th>投注内容/赔率</th>
          <th>彩果</th>
        </tr>
        <tr v-for="n in details.order_list" :key="n.id">
          <td><p v-html="time(n.game_start_time,true)"></p></td>
          <td><p><img :src="n.icon" alt="游戏" style="width: 2em;height: 2em;"></p></td>
          <td><p>{{ n.host_team_name }}</p><p>VS</p><p>{{ n.guest_team_name }}</p></td>
          <td>
            <p :class="{'state-0': status(n.action_type,n.distribute_status,n.winnings_status)===0}">
              {{n.round|inning}}<br>
              {{n.bet_team_name}}<br>
              {{n.bet_play_name}}<br>
              {{n.bet_odds|odds}}</p>
          </td>
          <td><p v-html="Fruit(n)"></p></td>
        </tr>
      </table>
    </div>
  </div>
</template>
<script>//
import http from '../../../tool/http'
import { timeFmt } from '../../../tool/util'

function inning (value) {
  if (!value || value === 0) return '全局'
  const arr = ['十', '一', '二', '三', '四', '五', '六', '七', '八', '九']
  value = value.toString()
  if (value < 10) {
    return `第${arr[value]}局`
  } else if (value < 20) {
    return `第十${arr[value[1]]}局`
  } else if (value < 100) {
    return `第${arr[value[0]]}十${arr[value[1]]}局`
  } else {
    return `第${value}局`
  }
}

function odds (value) {
  if (value && value > 1000) {
    return value / 1000
  }
  return value
}
export default {
  name: 'BettingDetails',
  data () {
    return {
      details: {
        order_list: [],
        total_amount: '加载中...',
        total_odds: '加载中...',
        winnings_bonus: '加载中...',
        bet_time: 1543482745
      }
    }
  },
  methods: {
    srcTxt (length, state) {
      if (length === 1) {
        return state ? `早盘` : require(`../../../assets/store/365/ic_zaopan.png`)
      } else if (length > 1) {
        return state ? `过关 ${length}串1` : require(`../../../assets/store/365/ic_guoguan.png`)
      }
      return state ? '加载中...' : require(`../../../assets/home/ic_placepic@2x.png`)
    },
    time (time, type) {
      let arr = timeFmt(time, 'MM-dd hh:mm')
      if (type) {
        arr = arr.replace(' ', '<br>')
      }
      return arr
    },
    status (t, d, w, type) {
      // d派奖状态
      // 开奖状态
      let [s0, s1, s2, s3] = ['state-0', 'state-1', 'state-2', 'state-3']
      if (type) {
        d === undefined && (d = this.$route.query.distribute_status)
        w === undefined && (d = this.$route.query.winnings_status)
      } else {
        [s0, s1, s2, s3] = [0, 1, 2, 3]
        // 0中奖1未中奖2待开3取消
      }
      if (d === 0 && w === 0) {
        return s2
      } else if (d === 1 && w === 0) {
        return s1
      } else if (d === 1 && w === 1) {
        if (t === 1) {
          return s3
        }
        return s0
      }
      if (w === 1) {
        if (t === 1) {
          return s3
        }
        return s0
      } else if (w === -1) {
        return s1
      } else {
        return s2
      }
    },
    Fruit (n) {
      // 彩果显示
      let html = ``
      const status = this.status(n.action_type, n.distribute_status, n.winnings_status)
      if (status === 0) {
        // 中奖
        html = `<p>${inning(n.round)}<br>${n.bet_team_name}<br>${n.bet_play_name}<br>${odds(n.bet_odds)}</p>`
      } else if (status === 1) {
        // 未中奖
        html = `<p>${inning(n.round)}<br>${n.bet_team_name === n.host_team_name ? n.guest_team_name : n.host_team_name}<br>${n.bet_play_name}<br>${odds(n.bet_team_name === n.host_team_name ? n.guest_team_odd : n.host_team_odd)}</p>`
      } else if (status === 3) {
        // 取消
        html = `<p style="color: #FF3333">取消</p>`
      }
      return html
    }
  },
  created () {
    http.getBetOrderDetail(this.$route.query.id).then(val => {
      if (val) {
        this.details = val
      }
    })
  },
  filters: {
    inning,
    odds
  }
}
</script>
<style scoped lang="scss">
  .betting-details {
    .order-list-entry {
      margin: 5px 10px 0 10px;
      border-radius: 4px;
      display: flex;
      flex-flow: row nowrap;
      align-items: center;
      padding-left: 10px;
      background-image: url("../../../assets/store/365/card_title_bg.png");
      background-position: left bottom;
      background-repeat: no-repeat;
      background-size: 100% 100%;
      height: 70px;
      > div:nth-of-type(1) {
        flex: 1;
        margin: 0 10px;
        > div {
          display: flex;
          flex-flow: row nowrap;
          align-items: center;
          justify-content: space-between;
          height: 30px;
          p {
            font-size: 12px;
            color: #999999;
            &:last-child {
              text-align: right;
              flex: 1;
            }
          }
        }
        > div:nth-child(1) {
          border-bottom: 1px solid #494949;
          > p {
            font-size: 13px;
            &.state-0:after {
              content: '中奖';
              color: #FFC63A;
            }
            &.state-1:after {
              content: '未中奖';
            }
            &.state-2:after {
              content: '待开奖';
            }
            &.state-3:after {
              content: '未中奖';
            }
          }
        }
        > div:nth-child(2) {
          /*border-bottom: 1px solid #494949;*/
          > p:nth-child(2) {
            &:before {
              content: '中奖金额:';
            }
            &.state-2 {
              display: none;
            }
            &.state-3:before {
              content: '返还金额:';
              color: #FF3333;
            }
          }
        }
      }
    }
    .betting-form-connect {
      margin: 0 10px;
      height: 10px;
      overflow: hidden;
      > img {
        display: block;
        width: 100%;
      }
    }
    .betting-form {
      > div.form-head {
        display: flex;
        flex-flow: row nowrap;
        align-items: center;
        justify-content: space-between;
        background-color: #313131;
        height: 30px;
        p {
          font-size: 12px;
          color: #ccc;
          &:first-child {
            text-indent: 10px;
          }
          &:last-child {
            padding-right: 10px;
            white-space: nowrap;
          }
        }
      }
      > table {
        background-color: #313131;
        width: 100%;
        /*table-layout: fixed;*/
        border-radius: 0 0 4px 4px;
        th {
          font-size: 13px;
        }
        td {
          font-size: 12px;

          .state-0 {
            color: #FFC63A;
          }
          .state-0:after {
            content: '';
            display: inline-block;
            width: 16px;
            height: 12px;
            background-image: url("../../../assets/store/365/ic_book_correct@2x.png");
            background-size: 100% 100%;
            background-repeat: no-repeat;
            background-position: center;
          }

          /*.state-1:after {*/
          /*content: '✖';*/
          /*color: #999999;*/
          /*}*/
          /*.state-2:after {*/
          /*content: '待开奖';*/
          /*}*/
          /*.state-3:after {*/
          /*content: '取消';*/
          /*color: #FF3333;*/
          /*}*/
        }
        td p {
          transform: scale(.95);
        }
        td, th {
          border-top: 1px solid #494949;
          border-right: 1px solid #494949;
          text-align: center;
          padding: 9px 0;
          color: #CCCCCC;
        }
        > tr > td:last-child, > tr > th:last-child {
          border-right: none;
        }
        tr td:first-child > p:first-child, th, tr td:last-child > p {
          white-space: nowrap;
        }
      }
    }
  }
</style>
