<template>
  <div>
    <nav-head title="我的红包" fixed-or-transparency="NoTransparency"></nav-head>
    <no-content v-show="showContent" img="5" :text="['参与活动获得更多红包!']"/>
    <div class="no-red-button" v-show="CouponList.length">
      <p>不使用红包</p>
      <div @click="ClickSelection({target:$route.query.target, redId: 0})"></div>
    </div>
    <ul class="red-package-container">
      <li class="red-package-item" v-for="i in CouponList" :key="i.id"
          @click="ClickSelection({target:$route.query.target, redId:i.id})">
        <div class="red-package-item-top">
          <div>
            <p>{{i.amount}}</p>
            <p>{{i.desc}}</p>
          </div>
          <div>
            <p>{{i.coupon_type===1? '普通红包':'满减红包'}}</p>
            <div :class="checkRed(i.id)?'yes':'no'"></div>
          </div>
        </div>
        <div class="red-package-item-bottom">
          <p></p>
          <p>{{i.end_time|dateFormatDay('yyyy-MM-dd')}}到期</p>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>//
import { mapActions, mapState } from 'vuex'
import { GET_COUPON_LIST } from '../../../vuex/Mine'
import { SELECTION_RED_ENVELOPES } from '../../../vuex/365/home'
import { Toast } from 'mint-ui'

export default {
  name: 'RedPacketsSelected',
  data () {
    return {
      showContent: false
    }
  },
  created () {
    this.getCouponList()
  },
  computed: {
    ...mapState({
      Coupon: state => state.mine.Coupon,
      Selection: state => state.home.PassStatus.UseRed
    }),
    CouponList () {
      const { supportType, activityId } = this.$route.query
      const tate = new Date().getTime() / 1000
      return this.Coupon.filter(i => {
        if (i.expire_status * 1 === 1) {
          // 未过期
          if ((i.support_type * 1 === 0 || supportType * 1 === i.support_type * 1) && (i.activity_id * 1 === 0 || activityId * 1 === i.activity_id * 1)) {
            // support_type支持红包支持彩种 0 所有 1电竞 2足球 3篮球
            // activity_id活动ID （0 不关联活动）
            if (tate >= i.start_time) {
              // 开始使用时间
              return true
            }
          }
        }
        return false
      })
    }
  },
  methods: {
    ...mapActions({
      getCouponList: GET_COUPON_LIST,
      Choice: SELECTION_RED_ENVELOPES
    }),
    checkRed (id) {
      for (let i in this.Selection) {
        if (this.Selection[i] && this.Selection[i].id === id) {
          return true
        }
      }
      return false
    },
    ClickSelection (params) {
      if (params.target !== 'pass') {
        // 早盘
        if (this.Selection[params.target] && this.Selection[params.target].id === params.redId) {
        } else if (this.checkRed(params.redId)) {
          // (已有)
          Toast('已有比赛选中')
          return
        }
      }
      this.Choice(params)
      this.$router.back()
    }
  },
  watch: {
    CouponList: function (ne) {
      this.showContent = !ne.length
    }
  }
}
</script>

<style scoped lang="scss">
  .no-red-button {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: flex-start;
    background-color: #1C1C1C;
    margin: 10px;

    > p {
      color: #FFC63A;
      font-size: 13px;
      flex: 1;
      text-indent: 10px;
    }

    > div {
      margin: 10px;
      width: 20px;
      height: 20px;
      background-image: url("../../../assets/mine/365/ic_sel.png");
      background-position: center;
      background-repeat: no-repeat;
      background-size: 100% 100%;
    }
  }

  .red-package-container {
    .red-package-item {
      margin: 9px 10px;

      &.Be-overdue {
        > .red-package-item-top {
          background-image: url("../../../assets/mine/365/card_title_bg_passed.png");

          > div:last-child {
            > p {
              width: 43px;
              height: 43px;
              background-size: 100% 100%;
              background-position: center;
              background-repeat: no-repeat;
              background-image: url("../../../assets/mine/365/ic_passed.png");
            }

            > div {
              background-image: url("../../../assets/home/365/ic_clean@2x.png");
              display: inline-block;
            }
          }
        }
      }

      > .red-package-item-top {
        padding: 0 10px;
        display: flex;
        flex-flow: row nowrap;
        align-items: center;
        justify-content: flex-start;
        height: 64px;
        background-image: url("../../../assets/mine/365/card_title_bg_nor.png");
        background-position: center;
        background-repeat: no-repeat;
        background-size: 100% 100%;
        border-top-left-radius: 4px;
        border-top-right-radius: 4px;
        color: #FFFFFF;

        > div:first-child {
          flex: 1;

          > p:first-child {
            &:before {
              content: '¥';
              font-size: 12px;
              vertical-align: top;
              margin-right: 3px;
              line-height: 17px;
            }

            &:after {
              content: '元';
              font-size: 12px;
              line-height: 17px;
              vertical-align: middle;
            }

            font-size: 17px;
            font-weight: bold;
            line-height: 1;
          }

          > p:last-child {
            margin-top: 8px;
            line-height: 1;
          }
        }

        > div:last-child {
          display: flex;
          flex-flow: row nowrap;
          align-items: center;
          justify-content: flex-start;

          > p {
            font-size: 15px;
            line-height: 1;
            font-weight: bold;
          }

          > div {
            width: 20px;
            height: 20px;
            background-size: 100% 100%;
            background-position: center;
            background-repeat: no-repeat;
            margin: 10px;
            margin-right: 0;
            display: none;

            &.yes {
              display: inline-block;
              background-image: url("../../../assets/mine/365/ic_sel(1).png");
            }

            &.no {
              display: inline-block;
              background-image: url("../../../assets/mine/365/ic_nor.png");
            }
          }
        }
      }

      > .red-package-item-bottom {
        padding: 0 10px;
        display: flex;
        flex-flow: row nowrap;
        align-items: center;
        justify-content: flex-start;
        background-image: url("../../../assets/mine/365/card_date_bg.png");
        background-position: center;
        background-repeat: no-repeat;
        background-size: 100% 100%;
        height: 26px;
        width: 100%;

        > p:last-child {
          flex: 1;
          text-align: right;
          color: #999999;
        }
      }
    }
  }
</style>
