<template>
  <div class="item-wrap">
    <template v-if="item.expire_status === 1">
      <div class="item">
        <p class="coupon-data">
          ￥<span class="coupon-amount">{{item.amount}}</span>元
        <span class="coupon-limit">满{{item.coupon_min_consume_price}}元才可使用</span>
          <template v-if="item.coupon_type === 1">
            <span class="badge">普通红包</span>
          </template>
          <template v-else-if="item.coupon_type === 2">
            <span class="badge">满减红包</span>
          </template>
        </p>
        <img class="circle" src="../../assets/mine/365/circle@2x.png"/>
        <p class="coupon-time">{{ time(item.end_time) }} 到期</p>
      </div>
    </template>
    <template v-else-if="item.expire_status === 0">
      <div class="item">
        <p class="coupon-data expire">
          ￥<span class="coupon-amount">{{item.amount}}</span>元
        <span class="coupon-limit">满{{item.coupon_min_consume_price}}元才可使用</span>
          <img src="../../assets/mine/365/ic_passed@2x.png"/>
        </p>
        <img class="circle" src="../../assets/mine/365/circle@2x.png"/>
        <p class="coupon-time">
          <mt-button @click.native="deleteHandler(item.id)" class="btn-delete">删除</mt-button>
          {{ time(item.end_time) }} 到期
        </p>
      </div>
    </template>
  </div>
</template>

<script>//
import { Button } from 'mint-ui'

export default {
  name: 'CouponItem',
  props: {
    item: { type: Object }
  },
  methods: {
    time (value) {
      var time = new Date(value * 1000)
      return time.getFullYear() + '-' + time.getMonth() + 1 + '-' + time.getDate()
    },
    deleteHandler (val) {
      this.$emit('handler', val)
    }
  },
  components: {
    [Button.name]: Button
  }
}
</script>

<style scoped lang="scss">
  .item-wrap {
    border: none;
    outline: none;
    background: none;
    text-align: left;
    margin-top: 15px;
    .item {
      border: none;
      outline: none;
      background: none;
      width: 92.5%;
      margin: 0 auto;
      text-align: left;
      .coupon-data {
        position: relative;
        font-size: 16px;
        line-height: 26px;
        color: white;
        padding: 4% 3% 4% 6%;
        background-image: url("../../assets/mine/365/card_title_bg@2x.png");
        background-size: 100% 100%;
        background-position: left top;
        background-repeat: no-repeat;
        .coupon-amount {
          font-size: 20px;
        }
        .coupon-limit {
          font-size: 12px;display: block;margin-top: -3px;
        }
        .badge {
          position: absolute;
          top: 0;
          right: 15px;
          display: inline-block;
          color: white;
          font-size: 15px;
          float: right;
          margin-top: 7.5%;
        }
      }
      .expire {
        background-image: url("../../assets/mine/365/redcard_bg_passed@2x.png");
        img {
          position: absolute;
          top: 0;
          right: 18px;
          margin-top: 3.5%;
          width: 14.5%;
        }
      }
      .coupon-time {
        background-color: #313131;
        color: #999;
        text-align: right;
        font-size: 13px;
        padding: 10px 15px;
        border-bottom-left-radius: 6px;
        border-bottom-right-radius: 6px;
        .btn-delete {
          color: #FF3333;
          font-size: 14px;
          background: none;
          border: none;
          outline: none;
          box-shadow: none;
          float: left;
          height: auto;
          padding: 0;
          margin: 0;
        }
      }
      .circle {
        position: relative;
        z-index: 2;
        float: left;
        width: 100%;
        margin-top: -1%;
      }
    }
  }
</style>
