<template>
  <div class="couponList">
    <nav-head title="我的红包" fixed-or-transparency="isFixed NoTransparency"></nav-head>
    <div class="coupon-list">
      <div v-if="CouponList.length > 0">
        <coupon-item
          v-for="item in CouponList"
          :item="item"
          :key="item.id"
          @handler="deleteCouponHandler"
        />
      </div>
      <no-content v-else img="5" :text="['参与活动获得更多红包!']"/>
    </div>
  </div>
</template>

<script>//
import { mapActions, mapState } from 'vuex'
import { GET_COUPON_LIST, DELETE_COUPON } from '../../../vuex/Mine'
import CouponItem from '../../../components/365/CouponItem.vue'

export default {
  name: 'RedEnvelopesList',
  methods: {
    deleteCouponHandler (couponId) {
      if (couponId) {
        this.deleteCoupon(couponId)
      }
    },
    ...mapActions({
      getCouponList: GET_COUPON_LIST,
      deleteCoupon: DELETE_COUPON
    })
  },
  components: {
    CouponItem
  },
  computed: {
    ...mapState({
      CouponList: state => state.mine.Coupon
    })
  },
  created () {
    this.getCouponList()
  }
}
</script>

<style scoped lang="scss">
  .couponList {
    position: relative;
    z-index: 3;
    min-height: 100vh;
    background-color: $background-light-color;
    text-align: center;
    div.coupon-list {
      padding-bottom: 20px;
    }
  }
</style>
